<template>
  <div>
    <Teleport to=".base-layer">
      <transition name="fade">
        <section class="modal" v-if="isModalOpen" @click.self="ccCloseModal" :data-id="item.id" :data-type="item.type">
          <div class="modal-wrap">
            <div class="modal-header" v-if="$slots.head">
              <slot name="head"></slot>
            </div>
            <div class="modal-content">
              <slot name="body"></slot>
            </div>
            <div class="modal-footer" v-if="$slots.foot">
              <slot name="foot"></slot>
            </div>
          </div>
        </section>
      </transition>
    </Teleport>
  </div>
</template>
<script setup>
import { ref, defineProps, defineEmits, watch, nextTick } from 'vue'

const props = defineProps({
  modalValue: Boolean,
  dataList: Object,
})

const emit = defineEmits(['update:modelValue', 'close']);
const isModalOpen = ref(props.modalValue);
const item = ref(props.dataList);

// watch(() => props.modalValue, (newVal) => {
//   isModalOpen.value = newVal;
//   console.log(modal, isModalOpen.value);
// })

watch(() => props.modalValue, async (newVal) => {
  isModalOpen.value = newVal;
  await nextTick();   // Wait for DOM update
  const modal = document.querySelector('.modal'); // Update the DOM element
  const baseLayer = document.querySelector('.base-layer');

  if (modal) {
    modal.setAttribute('aria-hidden', !newVal);

    if(newVal) {
      baseLayer.dataset.layer = 'true';
    }
  }
});

watch(() => props.dataList, (newData) => {
  item.value = newData;
})

const ccCloseModal = () => {
  closeModal();
}

const closeModal = () => {
  // isModalOpen.value = false;
  emit('update:modalValue', false);
  console.log('잘 닫아졌습니다.ㅇ')
  emit('close');
};
</script>