<template>
    
        <div class="btn-list">
            <button @click="openModal('modal_data_01')" class="btn t2">팝업1</button>
            <button @click="openModal('modal_data_02')" class="btn t2">팝업2</button>
        </div>
        
        <ModalCont 
            :data-list="currentModalDate" 
            v-model:modalValue="isModalOpen"
            @close="closeModal_parent"
            >
            <template #head>
                <h1>{{ currentModalDate.headText }}</h1>
            </template>
            <template #body>
                <p>{{ currentModalDate.bodyText }}</p>
            </template>
            <template #foot>
                <button
                    v-for="(btn, index) in currentModalDate.footerBtns" 
                    :key="index"
                    :type="btn.type"
                    class="btn"
                    :class="btn.class"
                    @click="btn.action"
                    >
                    <span>{{ btn.text }}</span>
                </button>
            </template>
        </ModalCont>
    

</template>
<script setup>
import { ref } from 'vue'
import ModalCont from '@/components/ModalCont.vue'


// 상태관리
const isModalOpen = ref(false);
const currentModalDate = ref({});

const openModal = (key) => {
    isModalOpen.value = true;
    currentModalDate.value = modalData[key];

    // 모달을 열 때 baseLayer 업데이트
    const baseLayer = document.querySelector('.base-layer');
    if (baseLayer) {
        baseLayer.dataset.layer = 'true';
    }
}

const closeModal_parent = () => {
    isModalOpen.value = false;
    console.log('닫기 버튼 동작');
    const baseLayer = document.querySelector('.base-layer');
    
    if (baseLayer) {
        baseLayer.dataset.layer = 'false';
    }
}

const modalData = {
    modal_data_01: {
        headText: '첫번째 팝업제목',
        bodyText: '첫번째 팝업의 내용들',
        footerBtns : [
            {
                text: '취소',
                type: 'button',
                action: closeModal_parent,
                class: 'second',
            },
            {
                text: '확인',
                type: 'button',
                action: closeModal_parent,
                class: 'primary',
            }
        ],
        type: 'info',
        id: 'modal-a',
    },
    modal_data_02: {
        headText: '두번째 팝업 타이틀',
        bodyText: '두번째 팝업의 내용들',
        footerBtns : [
            {
                text: '취소',
                type: 'button',
                action: closeModal_parent,
                class: 'second',
            },
            {
                text: '확인',
                type: 'button',
                action: closeModal_parent,
                class: 'primary',
            }
        ],
        type: 'alert',
        id: 'modal-b',
    }
}

</script>
